import content from '../../components/xyz-masonry/xyz-masonry.vue'
export default{
	data(){
		return {
			ismH:9999,
			currentTab: 0,
			tabClick: 0,
			navIndex:'',
			dataList:[],
			isPage:1,
			addPage:1,
			iscid:1,
			jobArr:'',
			jobId:'',
			cIndex:1,
			inputContent:'',
			Height: '',
			stateBarHeight: ''
		}
	},
	components: {
		content
	},
	computed:{
		isModel:function(){
			return this.$store.state.model;
		},
		isHeight:function(){
			return this.$store.state.windowHeight-51
		}
	},
	onLoad(option) {
		this.inputContent=option.content
		this.Height=this.$store.state.windowHeight
		this.stateBarHeight = this.$store.state.statusBarHeight
	},
	onReady() {
		this.getResult();
		uni.loadFontFace ({
		  family: 'Alibaba PuHuiTi',
		  source: 'url("https://www.mastervan.cn/static/project/dolphin/static/font/alifont/AlibabaPuHuiTi-Light.ttf")',
		  success: function(){
				console.log('load font success')
			  },
			fail() {
				//console.log(456)
			}
		})
	},
	methods: {
		// 请求事件
		isRequest() {
			return new Promise((resolve, reject) => {
				setTimeout(() => {
			
					resolve()
				}, 1000)
			})
		},
		lower1(){
			// uni.showLoading({
			// 	content: '加载中'
			// })
			
			let openid=uni.getStorageSync('openid');
			// console.log(openid)
			let _this=this;
	        console.log(_this.addPage)
			uni.request({
				url:_this.$store.state.baseurl+'/user/findCondition',
				data:{
					openId:openid,
					page:_this.addPage,
					cid:_this.cIndex
				},
				method:'POST',
				header : {'content-type':'application/x-www-form-urlencoded'},
				success:function(res){
					//uni.hideLoading()
					let list=res.data.data.list;
					console.log(list)
					if(list!=undefined){
						for(let i in list){
							_this.dataList.push(list[i]);
						}
						_this.addPage+=1
					}
					console.log(_this.dataList)
				}
			})
		},
		goback:function(){
			uni.navigateBack({
				delta:1
			})
		},
		getResult:function(){
			let openid=uni.getStorageSync('openid');
			// console.log(openid)
			let _this=this;
			uni.request({
				url:_this.$store.state.baseurl+'/user/findCondition',
				data:{
					openId:openid,
					page:_this.isPage,
					condition:_this.inputContent
				},
				method:'POST',
				header : {'content-type':'application/x-www-form-urlencoded'},
				success:function(res){
					//uni.hideLoading()
					let list=res.data.data.list;
					_this.dataList=list;
					_this.addPage+=1;
					//console.log(_this.dataList)
				}
			})
		}
	}
}
